@-webkit-keyframes spinRight {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


@keyframes spinRight {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@-webkit-keyframes spinLeft {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(-360deg);
                transform: rotate(-360deg);
    }
}

@keyframes spinLeft {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(-360deg);
                transform: rotate(-360deg);
    }
}
.loaderContainer{
    position:relative;
    width:120px;
    height:120px;
    /*background-color: black;*/
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin:0 auto;
}

.loader, .loaderInner , .loaderInnerSmall {
    position: absolute;    
    border-radius:50%;    
    border-top: 4px solid rgb(255,51,51);
    /* border-bottom: 4px solid rgb(255,51,51); */
    width: 100px;
    height: 100px;
   
}

.loader{
    -webkit-animation: spinRight 2s linear infinite;
    animation: spinRight 1s linear infinite;
    -webkit-animation-delay:.2s;
            animation-delay:.2s;
}



.loaderInner {
    width: 70px;
    height: 70px;
    -webkit-animation: spinRight 2s linear infinite;
    animation: spinRight 1s linear infinite;
    -webkit-animation-delay:.1s;
            animation-delay:.1s;
    
}

.loaderInnerSmall{
    width:40px;
    height:40px;
    -webkit-animation: spinRight 2s linear infinite;
    animation: spinRight 1s linear infinite;
    /* animation-delay:.2s; */
}

.registerButton{
    width:100%;
}

@-webkit-keyframes showMessageAlert{
    from{
        top: -200px;
        opacity: 0;
    }
    to{
        top:0;
        opacity:1;
    }
}

@keyframes showMessageAlert{
    from{
        top: -200px;
        opacity: 0;
    }
    to{
        top:0;
        opacity:1;
    }
}

.msgAlertActive{
    -webkit-animation-name: showMessageAlert;
            animation-name: showMessageAlert;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

.messageContainerAlert{
    position:absolute;
    width:60%;
    background-color:forestgreen;
    padding:16px;
    text-align:center;
     top:0;
     z-index: 10000;
    left:50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    top:0;
    opacity:0;
    
}

.tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    bottom: 150%;
    left: 50%;
    margin-left: -60px;
    font-size:15px;
    opacity: 0;
    -webkit-transition: opacity 1s;
    -o-transition: opacity 1s;
    transition: opacity 1s;
}

.tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left:50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: black transparent transparent transparent;
}


.setRead:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
}
.deleteMessage:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
}

.loginButton{
    width:100%;
}


